      selectProduct: 'week',
      modalTitle: this.$t('–ü–æ–¥–ø–∏—Å–∫–∞ –Ω–∞ –∞–ª–≥–æ—Ä–∏—Ç–º'),
      product: {
        auto_subscribe: false,
        is_active: true,
        is_new_payment: false,
        free_sale: false,
        finishDate: '',
        month: {
          sale: 0,
          price: 0
        },
        week: {
          sale: 0,
          price: 0
