      isConnected: 0,
      isLoadedData: false,
      isNotifyError: false,
      notifyErrorText: '',
      notifyContent: ''
    }
  },
  computed: {
    isPaymentValid: function () {
      return this.needAmount <= this.userBalance;
    },
    amountAfterPayment: function () {
      return this.userBalance - this.needAmount;
    },
    needForPayment: function () {
      return this.needAmount - this.userBalance;
    },
    needAmount: function () {
      return this.selected == 'week' ? this.product.week.price : this.product.month.price;
    }
  },
  methods: {
    getTextForAutoPaymentInfo: function () {
      return `<div class="border bl-border-yellow rounded-md drop-shadow-xl p-2 bg-white" style="font-size: 12px">
                <p>${this.$t('–ï—Å–ª–∏ –≤—ã –≤–∫–ª—é—á–∏—Ç–µ –ê–≤—Ç–æ–ø—Ä–æ–¥–ª–µ–Ω–∏–µ, —Ç–æ –ø–æ–¥–ø–∏—Å–∫–∞ –±—É–¥–µ—Ç –æ—Å—É—â–µ—Å—Ç–≤–ª—è—Ç—å—Å—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏.')}</p>
                <p>${this.$t('–ê–≤—Ç–æ–ø–æ–¥–ø–∏—Å–∫–∞ –Ω–µ —Å—Ä–∞–±–æ—Ç–∞–µ—Ç –≤ —Ç–æ–º —Å–ª—É—á–∞–µ:')}</p>
                <p>- ${this.$t('–ï—Å–ª–∏ —É –í–∞—Å –Ω–∞ —Å—á–µ—Ç—É –Ω–µ—Ç –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ–π —Å—É–º–º—ã –¥–ª—è –æ–ø–ª–∞—Ç—ã')}</p>
                <p>- ${this.$t('–ï—Å–ª–∏ –∞–≤—Ç–æ—Ä –∞–ª–≥–æ—Ä–∏—Ç–º–∞ –Ω–µ –∏–∑–º–µ–Ω–∏–ª —Ü–µ–Ω—É')}</p>
                <p>- ${this.$t('–ï—Å–ª–∏ –∞–≤—Ç–æ—Ä –∞–ª–≥–æ—Ä–∏—Ç–º–∞ –ø–æ–º–µ–Ω—è–ª —Å—Ä–æ–∫ –ø–æ–¥–ø–∏—Å–∫–∏')}</p>
      </div>`;
    },
    paymentBot: function () {

      if (this.product.is_new_payment
          || this.product.auto_subscribe != Boolean(parseInt(this.settings.userPaymentSettings.auto_subscribe))
          || this.product.is_active != Boolean(parseInt(this.settings.userPaymentSettings.is_active))
      ) {
        const paymentData = {
          costs: this.needAmount,
          scannerId: this.settings.paymentSettings.scanner_id,
          auto_subscribe: this.product.auto_subscribe,
          is_active: this.product.is_active,
          selected: this.selected,
          '_csrf': $('meta[name="csrf-token"]').attr('content')
        }
        this.isButtonDisabled = true;
        axios.post(URL_PAYMENTS_BOT, paymentData)
            .then(response => {
              this.notify = true;
              if (response.data.type == 'success') {
                this.botName = response.data.reason.bot_name;
                this.botSubscribeLink = response.data.reason.botSubscribeLink;
                this.isConnected = response.data.reason.is_connected;
                this.notifyText(response.data.reason);
              }
              if (response.data.type == 'error') {
                this.isNotifyError = true;
                this.notifyErrorText = response.data.reason;
              }
              this.isButtonDisabled = false;
            })
            .catch(error => {
              this.closeModal();
            });
      } else {
        this.closeModal();
      }
    },
    loadDataForPayment: function () {
      this.formData['_csrf'] = $('meta[name="csrf-token"]').attr('content'),
          axios.post(URL_GET_BOT_INFO_FOR_PAYMENTS, this.formData)
              .then(response => {
                this.parseResponse(response.data)
              })
              .catch(error => {
                alert(error);
                this.closeModal();
              });
    },
    parseResponse: function (resp) {
      if (resp.type == 'error') {
        this.notify = true;
        this.isNotifyError = true;
        this.notifyErrorText = resp.reason;
      } else {
        let data = resp.reason
        this.settings = data;
        if (data.paymentSettings) {
          this.product.free_sale = Boolean(parseInt(data.paymentSettings.free_sale));
          this.product.month.sale = Boolean(parseInt(data.paymentSettings.month_sale));
          this.product.month.sale = Boolean(parseInt(data.paymentSettings.month_sale));
          this.product.week.sale = Boolean(parseInt(data.paymentSettings.week_sale));
          this.product.month.price = parseInt(data.paymentSettings.price_per_month) / 100;
          this.product.week.price = parseInt(data.paymentSettings.price_per_week) / 100;
        }
        if (data.userBalance) {
          this.userBalance = data.userBalance / 100;
        }

        if (data.userPaymentSettings) {
          this.modalTitle = this.$t('–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ–¥–ø–∏—Å–∫–∏')
          this.product.auto_subscribe = Boolean(parseInt(data.userPaymentSettings.auto_subscribe));
          this.product.is_active = Boolean(parseInt(data.userPaymentSettings.is_active));
          this.product.is_new_payment = false;
          this.product.finishDate = data.userPaymentSettings.finish_date;
        } else {
          this.product.is_new_payment = true;
        }

        this.selected = this.product.free_sale ? 'free' : this.product.week.sale ? "week" : "month";
      }
      this.isLoadedData = true;
    },
    notifyText: function (reason) {
      if (reason.change) {
        this.notifyContent = reason.changeText;
      } else {
        this.notifyContent = this.$t('–°–ø–∞—Å–∏–±–æ –∑–∞ –ø–æ–¥–ø–∏—Å–∫—É –Ω–∞ –∞–ª–≥–æ—Ä–∏—Ç–º') + ' - ' + this.botName
      }
    },
    addBalance: function () {
      this.emitter.emit('setActiveModal', {setActiveComponent: 'CheckAmount', price: this.needForPayment});
    },
    closeModal() {
      this.isButtonDisabled = false;
      this.emitter.emit('balanceModal');
    }
  },
  mounted() {
    this.loadDataForPayment();
  }
}
</script>
